import React, { useState, useEffect } from "react";
import Papa from "papaparse";
import DataTable from "react-data-table-component";
import { CSVLink } from "react-csv";
import * as XLSX from "xlsx";
import "tailwindcss/tailwind.css";

const App = () => {
  const [csvData, setCsvData] = useState([]);
  const [filteredData, setFilteredData] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has("c") && urlParams.get("c") === "csv") {
      readClipboard();
    }
  }, []);

  const readClipboard = async () => {
    try {
      const text = await navigator.clipboard.readText();
      parseCsvData(text);
    } catch (err) {
      console.error("Failed to read clipboard contents: ", err);
    }
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      complete: (result) => {
        setCsvData(result.data);
        setFilteredData(result.data);
      },
    });
  };

  const parseCsvData = (data) => {
    const parsedData = Papa.parse(data, { header: true }).data;
    setCsvData(parsedData);
    setFilteredData(parsedData);
  };

  const handleRowSelected = (state) => {
    setSelectedRows(state.selectedRows);
  };

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearchTerm(value);
    const filtered = csvData.filter((row) =>
      Object.values(row).some((cell) =>
        String(cell).toLowerCase().includes(value.toLowerCase())
      )
    );
    setFilteredData(filtered);
  };

  const handleCellEdit = (rowIndex, columnId, value) => {
    const updatedData = [...filteredData];
    updatedData[rowIndex][columnId] = value;
    setFilteredData(updatedData);
    setCsvData(updatedData); // Update original data as well
  };

  const columns = Object.keys(csvData[0] || {}).map((key) => ({
    name: key,
    selector: (row) => row[key],
    sortable: true,
    cell: (row, rowIndex) => (
      <input
        type="text"
        value={row[key]}
        onChange={(e) => handleCellEdit(rowIndex, key, e.target.value)}
        className="p-1 border rounded"
      />
    ),
  }));

  const exportToXLS = (data, filename) => {
    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");
    XLSX.writeFile(workbook, filename);
  };

  return (
    <div
      className="App container mx-auto p-4"
      style={{
        border: "3px solid #99ccff",
        marginTop: "2px",
        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
      }}
    >
      <header className="bg-blue-500 text-white text-center p-4 mb-4 rounded">
        <h1 className="text-2xl">CSV Editor App</h1>
      </header>
      <nav className="flex justify-between items-center py-4">
        <div className="text-xl font-bold">CSV Editor</div>
        <div>
          <label className="block">
            <span className="sr-only">Upload CSV</span>
            <input
              type="file"
              accept=".csv"
              onChange={handleFileUpload}
              className="block w-full text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50"
            />
          </label>
        </div>
      </nav>
      {csvData.length > 0 && (
        <div className="flex justify-between items-center mb-4">
          <input
            type="text"
            value={searchTerm}
            onChange={handleSearch}
            placeholder="Search..."
            className="p-2 border rounded-md"
          />
        </div>
      )}
      <DataTable
        title="CSV Data"
        columns={columns}
        data={filteredData}
        selectableRows
        onSelectedRowsChange={handleRowSelected}
        pagination
        highlightOnHover
        striped
        className="mt-4"
      />
      {csvData.length > 0 && (
        <div className="mt-4 flex justify-between">
          <CSVLink
            data={csvData}
            filename={"edited-data.csv"}
            className="bg-blue-500 text-white py-2 px-4 rounded"
          >
            Save CSV
          </CSVLink>
          {selectedRows.length > 0 && (
            <>
              <CSVLink
                data={selectedRows}
                filename={"selected-rows.csv"}
                className="bg-green-500 text-white py-2 px-4 rounded"
              >
                Save Selected Rows
              </CSVLink>
              <button
                onClick={() => exportToXLS(selectedRows, "selected-rows.xlsx")}
                className="bg-orange-500 text-white py-2 px-4 rounded"
              >
                Export Selected to XLS
              </button>
            </>
          )}
          <button
            onClick={() => exportToXLS(csvData, "edited-data.xlsx")}
            className="bg-yellow-500 text-white py-2 px-4 rounded"
          >
            Export to XLS
          </button>
        </div>
      )}

      <div className="container mx-auto my-4">
        <footer className="text-center mt-8">
          Made in{" "}
          <a
            href="https://en.wikipedia.org/wiki/New_Hampshire"
            target="_blank"
            rel="noopener noreferrer"
            className="underline"
          >
            New Hampshire
          </a>{" "}
          with Love{" "}
          <span role="img" aria-label="Love emoji">
            
          </span>
        </footer>
      </div>
    </div>
  );
};

export default App;
